.todoItem {
  background-color: #fafafa;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
}
.todoItem-p {
  margin: 24px 0 24px 24px;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.todoItem-p--complete {
  text-decoration: line-through;
}
.icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}
.icon-check {
  position: absolute;
  left: 12px;
}
.icon-check--active {
  color: #4caf50;
}
.icon-delete {
  position: absolute;
  top: 16px;
  right: 24px;
}
.icon-delete:hover {
  color: red;
}
